import { Grid, Box, Stack, Typography, Container } from "@mui/material";
import { Path1, Dots } from "../Tools/IconsFactory";
import Spacing from "../Tools/Spacing";
import desk from "../../assets/imgs/desk.png";
import face from "../../assets/imgs/face.png";
import id from "../../assets/imgs/id.png";
import sign from "../../assets/imgs/sign.png";

export default function Features() {
  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12} lg={7} sx={{ display: { xs: "none", lg: "block" } }}>
          <ImgBox />
        </Grid>
        <Grid container item xs={12} lg={5} spacing={3} alignContent="center">
          <CardsChild cards={cards} />
        </Grid>
        <Grid container item xs={12} spacing={3}>
          <CardsChild cards={verticalCards} />
        </Grid>
      </Grid>
      <Spacing isSingle={true} />
      <SectionFooter />
      <Spacing isSingle={true} />
    </>
  );
}
const SectionFooter = () => {
  return (
    <Container maxWidth="md">
      <Stack direction="column">
        <Typography
          sx={classes.Title}
          style={{ textAlign: "center" }}
          paragraph
        >
          Mobile check-in: a unique platform to manage it all
        </Typography>
        <Typography sx={classes.Desc} style={{ textAlign: "center" }}>
          Make it easy for everyone ! Your guests check-in a simple to use app
          installed on tablet. Save time and embrace the contact-free check-in
          experience.
        </Typography>
      </Stack>
    </Container>
  );
};
const ImgBox = () => {
  return (
    <Box sx={classes.ImgBox}>
      <Path1 sx={classes.path} />
      <Dots sx={classes.Dots} />
    </Box>
  );
};

export const CardsChild = ({ cards }) => {
  return cards.items.map((card, index) => (
    <Grid
      key={index}
      item
      xs={cards.gridsize.xs}
      sm={cards.gridsize.sm}
      md={cards.gridsize.md}
      lg={cards.gridsize.lg}
      sx={cards.isVertical ? classes.VerticalGridcard : classes.Gridcard}
    >
      {cards.isVertical ? (
        <VerticalCard card={card} key={index} />
      ) : (
        <Card card={card} key={index} cards={cards} />
      )}
    </Grid>
  ));
};
const VerticalCard = ({ card }) => {
  return (
    <Box sx={classes.VerticalcardBox}>
      <Stack
        direction="column"
        spacing={2}
        sx={{ width: "100%", height: "100%" }}
      >
        <Typography sx={classes.Title}>{card.title}</Typography>
        <Typography sx={classes.Desc}>{card.desc}</Typography>
        <Typography sx={classes.Desc}>{card.subdesc}</Typography>
      </Stack>
    </Box>
  );
};
const Card = ({ card, cards }) => {
  return (
    <Box sx={classes.cardBox}>
      <Stack
        direction={{ xs: "column", sm: "row", md: "column", lg: "row" }}
        spacing={3}
        sx={{ width: "100%", height: "100%" }}
        alignItems={"center"}
      >
        {cards.noPic ? (
          false
        ) : (
          <img src={card.icon} alt="img" style={classes.icon} />
        )}
        <Box sx={{ width: "100%" }}>
          {card.soon ? (
            <>
              <Typography sx={classes.Title}>{card.title}</Typography>
              <Typography sx={classes.soon}>Coming Soon</Typography>
            </>
          ) : (
            <Typography sx={classes.Title} paragraph>
              {card.title}
            </Typography>
          )}
          <Typography sx={classes.Desc}>{card.desc}</Typography>
        </Box>
      </Stack>
    </Box>
  );
};

