import { Container, IconButton, Stack, useScrollTrigger } from "@mui/material";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import * as React from "react";
import { useState } from "react";
import { Logo } from "./Tools/IconsFactory";
// import Menu from "./Drawer";
function ElevationScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}

export default function Header() {
  const [state, setState] = useState(false);

  const toggleDrawer = () => {
    setState(!state);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <ElevationScroll>
        <AppBar sx={classes.Appbar}>
          <Toolbar>
            <Container maxWidth="xl" sx={classes.HeaderContainer}>
              <Box sx={classes.LogoBox}>
                <Logo sx={classes.Logo} viewBox="0 0 28.057 39.354" />
              </Box>
              <BrowserRender />
              <MobileRender toggleDrawer={toggleDrawer} />
            </Container>
          </Toolbar>
        </AppBar>
      </ElevationScroll>
      <Toolbar />
      <Toolbar />
      {/* <Menu state={state} toggleDrawer={toggleDrawer} /> */}
    </Box>
  );
}

const BrowserRender = () => {
  return (
    <Stack direction="row" spacing={4}>
      <div>
        <input
          type="radio"
          id="Eng"
          className="hiddenChebox"
          value={"Eng"}
          name="Lang"
        />
        <label htmlFor="Eng" className="VisibleChebox">
          Eng
        </label>
      </div>
      <div>
        <input
          type="radio"
          id="Fr"
          className="hiddenChebox"
          value={"Fr"}
          name="Lang"
        />
        <label htmlFor="Fr" className="VisibleChebox">
          Fr
        </label>
      </div>
    </Stack>
  );
};
const MobileRender = ({ toggleDrawer }) => {
  return (
    <Stack
      direction="row"
      spacing={4}
      sx={{ display: { xs: "flex", sm: "none" } }}
    >
      <IconButton onClick={toggleDrawer}>{/* <MenuRoundedIcon/> */}</IconButton>
    </Stack>
  );
};

